"use client";

import { useState, useEffect } from "react";
import { Map, Search, Filter, Users, MapPin, Navigation } from "lucide-react";

const CustomerMap = () => {
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        setTimeout(() => {
            setLoading(false);
        }, 1000);
    }, []);

    return (
        <div className="min-h-screen bg-gray-50 p-6">
            <div className="bg-white rounded-lg shadow-sm p-6 mb-6">
                <div className="flex items-center mb-4">
                    <Map className="w-8 h-8 text-blue-600 mr-3" />
                    <div>
                        <h1 className="text-2xl font-bold text-gray-900">
                            Bản đồ khách hàng
                        </h1>
                        <p className="text-gray-600">
                            Xem vị trí khách hàng trên bản đồ
                        </p>
                    </div>
                </div>
            </div>

            <div
                className="bg-white rounded-lg shadow-sm overflow-hidden"
                style={{ height: "600px" }}
            >
                {loading ? (
                    <div className="flex items-center justify-center h-full">
                        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
                    </div>
                ) : (
                    <div className="flex items-center justify-center h-full text-gray-500">
                        <div className="text-center">
                            <Map className="w-16 h-16 mx-auto mb-4 text-gray-400" />
                            <p className="text-lg font-medium">
                                Bản đồ sẽ được tích hợp ở đây
                            </p>
                            <p className="text-sm mt-2">
                                Cần tích hợp Google Maps hoặc OpenStreetMap
                            </p>
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
};

export default CustomerMap;
